import { NavLink } from 'react-router-dom';
import styles from './Navbar.module.css';

const Navbar = () => {
    return (
        <div className={styles.NavbarWrap}>
            <div className={styles.item}>
                <NavLink activeClassName={styles.activeLink} to={'/Dashboard'}>Dashboard</NavLink>
            </div>
            <div className={styles.item}>
                <NavLink activeClassName={styles.activeLink} to={'/Appointments'}>Appointments</NavLink>
            </div>
            <div>Doctors</div>
            <div>Departments</div>
            <div>Patients</div>
            <div>Payment</div>
            <div></div>
            <div>Help</div>
        </div>
    );
}

export default Navbar;