import styles from './Header.module.css';
import logo from './../../images/Header/logo.png';
import notification from './../../images/Header/notification.png'
import user from './../../images/Header/user.png'
import icoSearch from './../../images/Header/iconSearch.png'
const Header = () => {
    return (
        <div className={styles.headerWrap}>
            <div className={styles.Logo}>
                <img alt="" src={logo} />
            </div>

            <div className={styles.Search}>
                <div className={styles.icoSearch}><img alt="" src={icoSearch} /></div>
                <div>
                    <input placeholder="Search"></input>
                </div>

            </div>
            <div className={styles.UserDiv}>
                <div className={styles.SpanNotification}><img alt="" className={styles.notification} src={notification} /></div>
                <span className={styles.avatar}><img alt="" className={styles.user} src={user} /></span>
            </div>
        </div>
    );
}

export default Header;